import * as t from './../actions/actionTypes';

const initialState = {
    messages: null,
    count: 0
}

export default (state = initialState, action) => {
    console.log(action)
    switch (action.type) {
        case t.AUTH_SUBMIT:
            return Object.assign({}, state, {
                messages: action.data
            });
        default:
            return state;
    }
};