import * as t from './../actions/actionTypes';

const initialState = {
    messages: null,
    count: 0
}

export default (state = initialState, action) => {
    switch (action.type) {
        case t.INCREMENT:
            return {
                count: state.count + 1
            };
        case t.DECREMENT:
            return {
                count: state.count - 1
            };
        default:
            return state;
    }
};