import React, { Component } from 'react';
import ReactHtmlParser from 'react-html-parser';
import base_url from './../api/urlConfig'
import RequestUtil from '../api/request';

export default class FetchData extends Component {
    constructor(props) {
        super(props);
        this.state = {
            detail: [],
            imageList: [],
            attachmentList: [],
        };
    }

    componentDidMount = async () => {

        console.log("param1", this.props.match.params.id);

        console.log("param2", this.props.match.params.auth);


        RequestUtil.fetchJSON(`${base_url}/query`, 
            this.props.match.params.auth, {
            id: this.props.match.params.id,
        }).then((response) => {
            console.log(response.data)
            if (response.data) {
                let imageList = []
                let attachmentList = []
                const fileExtensionReg = /(?:\.([^.]+))?$/
                response.data.attachmentList.map(item => {
                    const fileExtension = fileExtensionReg.exec(item.url)[1]
                    switch (fileExtension) {
                        case 'jpg':
                        case 'jpeg':
                        case 'png':
                        case 'gif':
                            imageList.push({
                                ...item,
                                source: {
                                    uri: item.url,
                                }
                            })
                            break
                        default:
                            attachmentList.push(item)
                    }
                })
                this.setState({
                    detail: response.data.detail,
                    imageList,
                    attachmentList,
                })
            } else {
                console.log('error!!!')
            }
        }).catch((error) => {
            console.log(error)
        });

    };

    render() {

        const { date, detail, image, postedBy, title } = this.state.detail

        return (
            <div>
                <img src={image} alt="Lamp" width="100%" height="100%" />
                <p>{title}</p>
                <p>{date} {postedBy}</p>

                <div>{ReactHtmlParser(detail)}</div>
            </div>
        );
    }
}
