import React, { Component } from 'react';
import * as a from './../actions';
import { connect } from 'react-redux';
import Sub from './sub/SubComp';

class Auth extends Component {
    constructor(props) {
        super(props);
        this.state = { value: '' };
    }

    handleSendData = () => {
        this.props.sendTheAlert('hello world')
    }

    handleChange = (event) => {
        this.setState({ value: event.target.value });
    }

    handleSubmit = (event) => {
        alert('A name was submitted: ' + this.state.value);
        event.preventDefault();
    }

    callbackState = (val) => {
        console.log(val)
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <Sub callbackState={(val) => this.callbackState(val)} text='send data prop' />
                <h1>{this.props.messages}</h1>
                <label>
                    Name:
                    <input type="text" value={this.state.value} onChange={(event) => this.handleChange(event)} />
                </label>
                <input type="submit" value="Submit" />
                <button onClick={() => this.handleSendData()}>submit</button>
            </form >
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        sendTheAlert: (val) => dispatch(a.authSubmit(val))
    }
}

const mapStateToProps = state => {
    return {
        messages: state.auth.messages
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth);