import React from 'react';
import { createStore } from 'redux'
import { Provider } from 'react-redux';
import rootReducer from './reducers'
import { BrowserRouter, Route, Switch } from 'react-router-dom';

//import history from './components/History';

import Layout from './components/Layout';
import Home from './components/index';
import Example from './components/Example';
import Camara from './components/Camara';
import GeoLocation from './components/GeoLocation';
import Auth from './components/Auth';
import Counter from './components/Counter';
import FetchData from './components/FetchData';
import TymNews from './components/TymNews';
import './App.css';

//import store from './store';

const store = createStore(rootReducer)

function App() {
  return (
    <div class='body'>
      <Provider store={store}>
        <BrowserRouter basename={'/pwa'} >
          {/* <Router history={history}> */}
          <Switch>
            <Layout>
              <Route exact path={`/`} component={Home} />
              <Route exact path={`/camara`} component={Camara} />
              <Route exact path={`/geo`} component={GeoLocation} />
              <Route exact path={`/auth`} component={Auth} />
              <Route exact path={`/counter`} component={Counter} />
              <Route exact path={`/example`} component={Example} />
              <Route exact path={`/fetch/:catId`} component={FetchData} />
              <Route exact path={`/fetch/:catId`} component={FetchData} />
              <Route exact path={`/tymnews/:id/:auth`} component={TymNews} />
            </Layout>
          </Switch>
          {/* </Router> */}
        </BrowserRouter>
      </Provider>
    </div>
  );
}

export default App;
